import { debounce } from 'debounce';
import { NcButton, NcColorPicker, NcNoteCard } from '@nextcloud/vue';
import Undo from 'vue-material-design-icons/UndoVariant.vue';
import TextValueMixin from '../../mixins/admin/TextValueMixin.js';
export default {
  name: 'ColorPickerField',
  components: {
    NcButton,
    NcColorPicker,
    NcNoteCard,
    Undo
  },
  mixins: [TextValueMixin],
  props: {
    name: {
      type: String,
      required: true
    },
    value: {
      type: String,
      required: true
    },
    defaultValue: {
      type: String,
      required: true
    },
    displayName: {
      type: String,
      required: true
    }
  },
  methods: {
    debounceSave: debounce(async function () {
      await this.save();
    }, 200)
  }
};